header {
  top: 0;
  position: sticky;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1% 7%;
  padding-right: 5%;
  background: var(--offwhite);
  z-index: 1;
  padding-bottom: 20px;
  padding-top: 20px;
  transition: all 0.3s ease 0s;
  box-shadow: none;
}

.shadow {
  transition: all 0.3s ease 0s;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
}

.logo {
  cursor: pointer;
  height: 2rem;
  transition: all 0.3s ease 0s;
  transform: scale(1, 1);
}

.logo:hover {
  transition: all 0.3s ease 0s;
  transform: scale(1.1, 1.1);
}

.nav-links {
  list-style: none;
}

.nav-links li {
  display: inline-block;
  padding: 0px 20px;
}

.nav-links li a {
  transition: all 0.1s ease 0s;
}

.nav-links li a:hover {
  transition: all 0.1s ease 0s;
  padding-bottom: 5px;
  color: var(--dark_primary);
  border-style: solid;
  border-width: 0px 0px 2px 0px;
  border-color: var(--dark_primary);
}

li,
.nav-links li a {
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 16px;
  color: var(--gray3);
  text-decoration: none;
}

.nav-links ion-icon {
  font-size: 30px;
  display: inline-block;
  padding: 0px 20px;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.3s ease 0s;
  transform: scale(1, 1);
}

.nav-links ion-icon:hover {
  transition: all 0.3s ease 0s;
  transform: scale(1.3, 1.3);
}

.burger {
  display: none;
  cursor: pointer;
}

.burger div {
  width: 25px;
  height: 3px;
  background-color: var(--dark_primary);
  margin: 5px;
  transition: all 0.3s ease;
}

@media only screen and (max-width: 900px) {
  .burger {
    display: block;
  }

  .nav-links {
    position: absolute;
    right: 0px;
    height: 100vh;
    top: 10%;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
    justify-content: space-evenly;
    width: 40%;
    right: -45%;
    transition: 0.5s ease-in;
  }

  nav ul li,
  nav ul button,
  .nav-links ion-icon {
    opacity: 0;
  }

  header {
    background-color: white;
    justify-content: space-between;
    gap: 0px;
  }

  body {
    overflow-x: hidden;
  }

  .nav-links li a:hover {
    border-color: var(--dark_primary);
  }
}

.nav-active {
  right: 0%;
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

.toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
  opacity: 0;
}

.toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}
