.table-container-view {
  -moz-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.5);
  justify-content: center;
  text-align: center;
  align-items: center;
  max-width: 55vw;
  padding: 50px;
  padding-top: 30px;
  border-radius: 5px;
  margin: 40px auto;
  background-color: var(--offwhite);
  overflow-x: auto;
}

.container-view {
  -moz-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.5);
  justify-content: center;
  text-align: center;
  align-items: center;
  max-width: 60vw;
  padding: 50px;
  padding-top: 30px;
  border-radius: 5px;
  margin: 40px auto;
  background-color: var(--offwhite);
}

.table-container-view h1 {
  font-size: 50px;
  margin-bottom: 20px;
  display: block;
}

.form-view input {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.button-view {
  margin-top: 20px;
  width: 60%;
  font-size: 15px;
}

.form-view select {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  width: 205px;
}

@media only screen and (max-width: 925px) {
  .form-view input {
    margin: 8px 0px !important;
  }
}

.view-text {
  text-align: center;
  font-size: 50px;
}

.table-view td {
  padding: 30px 50px;
  text-align: left;
}

.table-view {
  margin: auto;
  border-collapse: collapse;
}

.table-view hr {
  width: 100%;
}

.table-view th {
  color: var(--gray2);
  padding: 0px 50px;
  text-align: left;
}

.table-view td {
  font-size: 23px;
}

.table-view tr {
  border-bottom: 1px solid black;
}

.position {
  color: var(--dark_primary);
  border: 2px solid var(--dark_primary);
  padding: 10px;
  font-size: 30px;
  border-radius: 5px;
}

.table-view tr:first-child td .position {
  padding-left: 14px;
  padding-right: 14px;
}

.meeting-link {
  text-align: center;
  font-size: 30px;
  margin-top: 20px;
}

.table-container-view::-webkit-scrollbar {
  width: 15px;
}

.table-container-view::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
}

.table-container-view::-webkit-scrollbar-thumb {
  border-radius: 15px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
}
