.container-signup {
  -moz-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.5);
  justify-content: center;
  text-align: center;
  align-items: center;
  max-width: 60vw;
  padding: 50px;
  padding-top: 30px;
  border-radius: 5px;
  margin: 40px auto;
  background-color: var(--offwhite);
}

.container-signup h1 {
  font-size: 50px;
  margin-bottom: 20px;
  display: block;
}

.form-signup input {
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.button-signup2 {
  margin-top: 20px;
  width: 60%;
  font-size: 15px;
}

.image-url-signup,
.meeting-url-signup,
.pass-reset-signup {
  width: 54%;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  border-radius: 5px;
}

.links-notification {
  color: var(--gray3);
  font-size: small;
}

@media only screen and (min-width: 890px) {
  .classCode-signup {
    margin-right: 10% !important;
  }
}
