:root {
  --dark_primary: #0072ff;
  --light_primary: #00c6ff;
  --gray1: #333333;
  --gray2: #4f4f4f;
  --gray3: #828282;
  --gray4: #bdbdbd;
  --gray5: #e0e0e0;
  --gray6: #f2f2f2;
  --offwhite: #f4f6f9;
}

.section1 {
  background-color: var(--offwhite);
  padding-top: 100px;
  display: inline-block;
  padding-bottom: 50px;
  width: 100%;
}

.flex-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  align-items: center;
}

.intro-text-container {
  position: relative;
  margin-left: 50px;
  max-width: auto;
  word-break: break-all;
}

.help {
  font-size: 75px;
  font-weight: bolder;
  background: white;
  background: -webkit-linear-gradient(to right, var(--light_primary), #0072ff);
  background: -moz-linear-gradient(to right, var(--light_primary), #0072ff);
  background: linear-gradient(to right, var(--light_primary), #0072ff);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  word-break: normal;
}

.automate {
  margin-left: 5px;
  margin-top: 20px;
  font-size: 25px;
  color: var(--gray3);
  font-weight: normal;
}

.break {
  flex-basis: 100%;
  height: 0;
}

.section2 {
  padding-top: 75px;
  padding-bottom: 75px;
  display: flexbox;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap;
  text-align: center;
  background: -webkit-linear-gradient(to right, var(--light_primary), #0072ff);
  background: -moz-linear-gradient(to right, var(--light_primary), #0072ff);
  background: linear-gradient(to right, var(--light_primary), #0072ff);
}

.enter-text-container {
  display: inline-block;
  text-align: center;
}

.visit-btn-container {
  display: inline-block;
}

button {
  padding: 8px 25px;
  color: white !important;
  background-color: transparent;
  border-radius: 4px;
  background-color: #0072ff;
  border: none;
  outline: none;
  cursor: pointer;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  transform: scale(1, 1);
}

.find-text-container {
  text-align: center;
}

button:hover {
  transition: all 0.3s ease 0s;
  transform: scale(1.1, 1.1);
}

@media only screen and (max-width: 700px) {
  .enter-text-container {
    margin-left: 10px;
    margin-right: 10px;
  }

  .section2 {
    padding-bottom: 50px;
  }
}

@media only screen and (max-width: 1160px) {
  .image1-container {
    margin-top: 80px;
  }

  .section1 {
    padding-bottom: 100px;
    padding-top: 50px;
  }
}

@media only screen and (max-width: 1095px) {
  .find-text-container {
    margin-top: 70px;
    text-align: center;
  }
}

.image1-container img {
  width: 300px;
}

.section3 {
  padding-top: 75px;
  padding-bottom: 15px;
  display: flexbox;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap;
  text-align: center;
  background: white;
}

#FAQ {
  font-weight: bolder;
  font-size: 50px;
  margin-bottom: 20px;
}

.faq-container {
  width: 95vw;
}

.faq {
  border-radius: 5px;
  margin-left: 50px;
  margin-right: 50px;
  padding-bottom: 30px;
  -moz-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.5);
  margin-bottom: 30px;
}

.preview {
  padding: 30px 30px 0px 30px;
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-style: normal;
  cursor: pointer;
}

.preview ion-icon {
  vertical-align: center;
  float: right;
  font-size: 10px;
  font-size: 40px;
}

.answer {
  text-align: left;
  margin-left: 30px;
  margin-right: 30px;
  color: var(--gray2);
  font-size: 16px;
  padding-top: 5px;
}

.section4 {
  text-align: center;
  font-weight: bolder;
}
