.section1-about {
  background: white;
  background: -webkit-linear-gradient(to right, var(--light_primary), #0072ff);
  background: -moz-linear-gradient(to right, var(--light_primary), #0072ff);
  background: linear-gradient(to right, var(--light_primary), #0072ff);
  padding-top: 5vh;
  display: inline-block;
  padding-bottom: 5vh;
  width: 100%;
}

.flex-container-about {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.section2-about {
  width: 50vw;
  background: white;
  padding: 50px;
}

.section2-about img {
  width: 100%;
}

.section2a-about {
  color: black;
  background-color: white;
  padding-top: 100px;
  padding-bottom: 100px;
  position: relative;
  overflow: none;
  white-space: nowrap;
  text-overflow: clip;
  width: 50vw;
  display: flex;
  justify-content: center;
}

.section2-overall-about {
  display: flex;
}

.section3-about {
  background-color: white;
}

.section3-about {
  padding-top: 55px;
  padding-bottom: 5px;
  padding-left: 50px;
  padding-right: 50px;
  display: flexbox;
  justify-content: space-between;
  align-content: center;
  flex-wrap: wrap;
  text-align: center;
}

@media only screen and (max-width: 700px) {
  .enter-text-container-about {
    margin-left: 10px;
    margin-right: 10px;
  }

  .section3-about {
    padding-bottom: 50px;
    padding-top: 0px;
  }
}

@media only screen and (max-width: 1180px) {
  .section1-about {
    padding-bottom: 100px;
    padding-top: 50px;
  }
}

@media only screen and (max-width: 800px) {
  .section2-about {
    width: 100vw;
    min-height: 500px;
  }

  .section2a-about {
    width: 100vw;
    padding-top: 0px;
    padding-bottom: 20px;
  }

  .section2-overall-about {
    flex-wrap: wrap;
  }

  .section2a-about {
    margin-bottom: 20px;
  }

  .section3-about {
    padding-top: 10px;
  }
}

.find-text-container-about {
  text-align: center;
}

@media only screen and (max-width: 1095px) {
  .find-text-container-about {
    margin-top: 70px;
  }
}

@media only screen and (min-width: 1500px) {
  .section2a-about {
    align-items: center;
  }
}

@media only screen and (max-width: 550px) {
  .image2-container-about video {
    max-width: 90vw;
  }
}

.image-1-container-about img {
  width: 400px;
}

.image-2-container-about img {
  width: 400px;
}

.profile-about {
  margin-right: 40px;
  margin-bottom: 40px;
}

.profile-about img {
  width: 150px;
  border-radius: 50%;
}

.name-about {
  font-weight: bold;
  font-size: 24px;
}

.role-about {
  color: var(--gray3);
  font-weight: bold;
}

.divide-about {
  border: 0;
  margin: auto;
  width: 75vw;
  height: 2px;
  background: var(--dark_primary);
  visibility: hidden;
}

@media only screen and (max-width: 800px) {
  .divide-about {
    visibility: visible;
  }
}

.break-about {
  flex-basis: 100%;
  height: 0;
}
